import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { formatterCurrency } from "../../../utils/string";
import { openModal } from "../../../actions/modal";
import { sellsTotal } from "./helper";
import YandexMap from "../../../components/features/modal/YandexMapModal";
import { useTranslation } from "react-i18next";

function Totals({ sells }) {
	const address = useSelector((state) => state.user.location.address);
	const dispatch = useDispatch();
	const { t } = useTranslation();
	function openMapModal(e) {
		dispatch(openModal("map"));
		e.preventDefault();
	}

	const totalPrice = sellsTotal(sells);
	const shippingPrice = 0;

	return (
		<div className="table-wrapper">
			<table className="table table-summary">
				<tbody>
					<tr className="summary-subtotal">
						<td>{t("subtotal")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(totalPrice)}
						</td>
					</tr>
					<tr className="summary-shipping">
						<td>{t("delivery")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(shippingPrice)}
						</td>
					</tr>
					<tr className="summary-shipping-estimate">
						<td
							style={{
								padding: "15px 0",
							}}
						>
							{address ? address.name : "Адрес не выбран"} :
						</td>
						<td
							style={{
								padding: "15px 0",
							}}
						>
							<button
								style={{
									padding: "0",
									marginTop: "5px",
									textDecoration: "underline",
								}}
								onClick={openMapModal}
							>
								{t("change-address")}
							</button>
						</td>
					</tr>

					<tr className="summary-total">
						<td>{t("total")}:</td>
						<td
							style={{
								width: "min-content",
							}}
						>
							{formatterCurrency(totalPrice)}
						</td>
					</tr>
				</tbody>
			</table>
			<YandexMap />
		</div>
	);
}

export default Totals;
