import React from "react";
import market from "../../../config/market";
import SellRecord from "./SellRecord";
import SellRecordTwo from "./SellRecordTwo";
import { useDispatch } from "react-redux";
import { removeFromCartState } from "../../../actions/cart";
import { useTranslation } from "react-i18next";
export default function CartBlock({ sells }) {
	const dispatch = useDispatch();
	const { t } = useTranslation();
	function removeAll(sells) {
		sells.forEach(sell => {
			dispatch(removeFromCartState(sell.data.variation.id));
		});
	}

	return (
		<div className="cart-block-main mt-2">
			<div className="filter-item" key="cat">
				{sells.map((sell, i) =>
					market.config.home !== 3 ? (
						<SellRecord key={i} sellRecord={sell} />
					) : (
						<SellRecordTwo key={i} sellRecord={sell} />
					)
				)}
				<button
					className="btn remove-all"
					onClick={() => removeAll(sells)}
				>
					{t("clear-all")}
				</button>
			</div>
		</div>
	);
}
