import React, { useEffect } from "react";
import { getImageUrl } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import InputChangerCount from "../../../components/common/InputChangerCount";
import { removeFromCartState, changeQty } from "../../../actions/cart";
import { useDispatch } from "react-redux";
import { formatterCurrency } from "../../../utils/string";
import { useTranslation } from "react-i18next";

function SellRecord({ sellRecord }) {
	const dispatch = useDispatch();
	const { data } = sellRecord;
	const { t } = useTranslation();
	let max_count = data.stock.count;
	let minCount = 1;

	useEffect(() => {
		quantityInputs();
	});

	function onChange(count, variationId) {
		if (!count) {
			removeFromCart(variationId);
		} else {
			dispatch(changeQty(variationId, count));
		}
	}

	function removeFromCart(variationId) {
		dispatch(removeFromCartState(variationId));
	}

	return (
		<div className="cart-item">
			<div className="row cart-product-card w-100">
				<div className="col-3">
					<Link
						className="product-media"
						to={`${process.env.PUBLIC_URL}/product/${data.variation.id}`}
					>
						<img
							style={{
								height: "100%",
								objectFit: "cover"
							}}
							src={getImageUrl(data.variation.images[0])}
							alt="Product"
						/>
					</Link>
				</div>
				<div
					className="row col-9 d-flex cart-product-info m-0 "
					style={{ minHeight: "100%" }}
				>
					<div className="col-12 cart-item-infoblock m-0 w-100">
						<Link
							className="text-dark"
							to={`${process.env.PUBLIC_URL}/product/${data.variation.id}`}
						>
							{data.variation.name}
						</Link>
						<div className="d-flex">
							{data.variation.properties.map((prop, index) => {
								return (
									<div
										key={index}
										className="cart-variation-pr"
									>
										<span>{prop.name}</span>
										<span>{prop.value}</span>
									</div>
								);
							})}
						</div>
						<div className="m-0">
							<div className="cart-price-perone">
								{formatterCurrency(data.newPrice)}{" "}
								{t("for-unit")}
							</div>
						</div>
					</div>
					<div className="col-12 cart-product-actions ml-auto">
						<div className="cart-amount-product">
							{formatterCurrency(sellRecord.sum)}
						</div>
						<InputChangerCount
							initValue={sellRecord.count}
							onChange={onChange}
							max_count={max_count}
							min={minCount}
							variationId={sellRecord.data.variation.id}
						/>
						<button
							className="btn-remove"
							onClick={() =>
								removeFromCart(sellRecord.data.variation.id)
							}
						>
							<i className="i-icon icon-times-circle-o"></i>
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}

export default SellRecord;
