import React from "react";
import Loader from "../features/loaders/loader";

function InputChangerCount({
	initValue,
	onChange,
	variationId,
	max_count,
	loadingRecord,
	min = 1,
	disable = true,
	plus,
	minus
}) {
	return (
		<div className="form-control input-changer-count d-flex p-3 justify-content-between">
			<span
				style={{ cursor: "pointer" }}
				onClick={() =>
					onChange(
						parseInt(initValue) - 1 >= min
							? parseInt(initValue) - 1
							: 0,
						variationId,
						max_count
					)
				}
			>
				{minus ? minus : "-"}
			</span>
			{loadingRecord ? (
				<div className="loader">
					<Loader />
				</div>
			) : (
				<input
					style={{
						textAlign: "center",
						border: "none",
						width: "100%",
						outline: "none"
					}}
					disabled={disable}
					onChange={e =>
						onChange(
							parseInt(e.target.value) >= min
								? parseInt(e.target.value)
								: min,
							variationId,
							max_count
						)
					}
					value={!initValue || initValue < 1 ? 1 : initValue}
					required
				/>
			)}
			<span
				style={{ cursor: "pointer" }}
				onClick={() =>
					onChange(parseInt(initValue) + 1, variationId, max_count)
				}
			>
				{plus ? plus : "+"}
			</span>
		</div>
	);
}

export default InputChangerCount;
