import React, { useEffect, useState } from "react";
import { getImageUrl, removeProps } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import InputChangerCount from "../../../components/common/InputChangerCount";
import { removeFromCartState, changeQty } from "../../../actions/cart";
import { useDispatch, useSelector } from "react-redux";
import { formatterCurrency } from "../../../utils/string";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";

function SellRecord({ sellRecord }) {
	const dispatch = useDispatch();
	const { data } = sellRecord;
	const { t } = useTranslation();
	const { settings } = useSelector(state => state.marketData);
	const [properties, setProperties] = useState();
	let max_count = data.stock.count;
	let minCount = 1;

	useEffect(() => {
		if (data.variation) {
			setProperties(
				removeProps(
					data.variation.properties,
					settings.variationProperty
				)
			);
		}
	}, [data.variation?.properties]);

	useEffect(() => {
		quantityInputs();
	});

	function onChange(count, variationId) {
		if (!count) {
			removeFromCart(variationId);
		} else {
			dispatch(changeQty(variationId, count));
		}
	}

	function removeFromCart(variationId) {
		dispatch(removeFromCartState(variationId));
	}

	return (
		<div className="cart-supplier-info cart-item osto-cart p-1 flex-column align-items-start">
			<div className="row cart-product-card w-100">
				<div className="col-lg-2 col-3">
					<Link
						className="product-media"
						to={`${process.env.PUBLIC_URL}/product/${data.variation.id}`}
					>
						<img
							style={{
								height: "100%",
								objectFit: "cover"
							}}
							src={getImageUrl(data.variation.images[0])}
							alt="Product"
						/>
					</Link>
				</div>
				<div
					className={`d-flex cart-product-info m-0 ${
						market.config.home === 8 ? `col-8` : `col-6`
					}`}
				>
					<div className="cart-item-infoblock m-0 w-100">
						<Link
							className="text-dark"
							to={`${process.env.PUBLIC_URL}/product/${data.variation.id}`}
						>
							{data.variation.name}
						</Link>
						<div className="d-flex">
							{data.variation ? (
								properties?.map((prop, index) => {
									return (
										<div
											key={index}
											className="cart-variation-pr"
										>
											<span>{prop.name}</span>
											<span>{prop.value}</span>
										</div>
									);
								})
							) : (
								<div className="short-line-shimmer"></div>
							)}
						</div>
						<div className="m-0">
							<div className="cart-price-perone">
								{formatterCurrency(data.newPrice)}{" "}
								{t("for-unit")}
							</div>
							<div className="cart-amount-product">
								{formatterCurrency(sellRecord.sum)}
							</div>
						</div>

						<div className="cart-price-mobileblock">
							<div className="mob-product-action">
								<InputChangerCount
									initValue={sellRecord.count}
									onChange={onChange}
									max_count={max_count}
									min={minCount}
									variationId={sellRecord.data.variation.id}
								/>
								<button
									className="btn-remove"
									onClick={() =>
										removeFromCart(
											sellRecord.data.variation.id
										)
									}
								>
									<i className="i-icon icon-times-circle-o"></i>
								</button>
							</div>
							<div className="cart-amount-product d-none">
								{formatterCurrency(sellRecord.sum)}
							</div>
						</div>
					</div>
				</div>
				<div
					className={`cart-product-actions ml-auto ${
						market.config.home === 8 ? `col-2` : `col-3`
					}`}
				>
					<InputChangerCount
						initValue={sellRecord.count}
						onChange={onChange}
						max_count={max_count}
						variationId={sellRecord.data.variation.id}
					/>
					<button
						className="btn-remove"
						onClick={() =>
							removeFromCart(sellRecord.data.variation.id)
						}
					>
						{market.config.home === 8 ? (
							<img
								src={market.config.icons.deleteIcon}
								alt="delete"
							/>
						) : (
							<i className="i-icon icon-times-circle-o"></i>
						)}
					</button>
				</div>
			</div>
		</div>
	);
}

export default SellRecord;
